$(".address-card").click(function () {
    let desc = $(this).attr("data-desc")
    let street = $(this).attr("data-street")
    let office = $(this).attr("data-office")
    let intercom = $(this).attr("data-intercom")
    let floor = $(this).attr("data-floor")
    let entrance = $(this).attr("data-entrance")
    let comment = $(this).attr("data-comment")

    $("._desc").html(desc)
    $("._street").val(street)
    $("._office").val(office)
    $("._intercom").val(intercom)
    $("._floor").val(floor)
    $("._entrance").val(entrance)
    $("._comment").val(comment)

    $(".address-form").addClass("address-form--active")
})


$("._save-address").click(function () {
    $(".address-form").removeClass("address-form--active")
})
